import React from 'react'
import { Button } from '@material-ui/core'

interface Props {
    value?: string
    color?: string
    children?: React.ReactNode
    onClick?: () => void
}

const CalcButton: React.FC<Props> = ({ value, color, children, onClick }) => {
    return (
        <Button variant="contained" style={{ height: '100%', width: '100%' }} value={value} onClick={onClick}>
            {children}
        </Button>
    )
}

export default CalcButton
