import React, { useState } from 'react'
import { Grid, Box, TextField } from '@material-ui/core'

import CalcButton from './components/CalcButton'

export const Calc: React.FC = () => {
    const [text, setText] = useState('')
    const [firstValue, setFirstValue] = useState('')
    const [operation, setOperation] = useState('')

    const removeLastNumberPressed = () => {
        setText(text.substr(0, text.length - 1))
    }

    const toggleSimbolPressed = () => {
        if (text.charAt(0) === '-') {
            setText(text.substr(1))
        } else {
            setText('-' + text)
        }
    }

    const addNumber = (value: string) => {
        setText(text + value)
    }

    const operatorPressed = (value: string) => {
        setFirstValue(text)
        setOperation(value)
        setText('')
    }

    const resultPressed = () => {
        result()
        resetFisrtValue()
        resetOperation()
    }

    const result = () => {
        let result = 0
        const v1: number = +firstValue
        const v2: number = +text

        switch (operation) {
        case '+':
            result = v1 + v2
            break
        case '-':
            result = v1 - v2
            break
        case '*':
            result = v1 * v2
            break
        case '/':
            result = v1 / v2
            break
        case '%':
            result = +text / 100
            break
        case '1/x':
            result = 1 / +text
            break
        default:
            result = 0
        }

        setText('' + result)
    }

    const reset = () => {
        setText('')
        resetOperation()
        resetFisrtValue()
    }

    const resetFisrtValue = () => {
        setFirstValue('')
    }

    const resetOperation = () => {
        setOperation('')
    }

    return (
        <Box border={1} borderColor="grey.500" boxShadow={3}>
            <Grid container direction="row" spacing={2} justify="center" alignItems="stretch">
                <Grid item xs={12}>
                    <TextField
                        id="text"
                        label=""
                        type="text"
                        variant="outlined"
                        inputProps={{ min: 0, style: { textAlign: 'right' } }}
                        style={{ height: '100%', width: '100%' }}
                        value={text}
                        onChange={e => {
                            setText(e.target.value)
                        }}
                    />
                </Grid>

                <Grid item xs={2}>
                    <CalcButton
                        value="MC"
                        onClick={() => {
                            reset()
                        }}>
                        MC
                    </CalcButton>
                </Grid>
                <Grid item xs={2}>
                    <CalcButton>MR</CalcButton>
                </Grid>
                <Grid item xs={2}>
                    <CalcButton>MS-</CalcButton>
                </Grid>
                <Grid item xs={2}>
                    <CalcButton>M+</CalcButton>
                </Grid>
                <Grid item xs={4}>
                    <CalcButton>M-</CalcButton>
                </Grid>

                <Grid item xs={2}>
                    <CalcButton
                        value="backspace"
                        onClick={() => {
                            removeLastNumberPressed()
                        }}>
                        &#60;&#10134;
                    </CalcButton>
                </Grid>
                <Grid item xs={2}>
                    <CalcButton>CE</CalcButton>
                </Grid>
                <Grid item xs={2}>
                    <CalcButton>C</CalcButton>
                </Grid>
                <Grid item xs={2}>
                    <CalcButton
                        value="+-"
                        onClick={() => {
                            toggleSimbolPressed()
                        }}>
                        &#177;
                    </CalcButton>
                </Grid>
                <Grid item xs={4}>
                    <CalcButton value="Square">&#8730;</CalcButton>
                </Grid>

                <Grid item xs={2}>
                    <CalcButton
                        onClick={() => {
                            addNumber('7')
                        }}>
                        7
                    </CalcButton>
                </Grid>
                <Grid item xs={2}>
                    <CalcButton
                        onClick={() => {
                            addNumber('8')
                        }}>
                        8
                    </CalcButton>
                </Grid>
                <Grid item xs={2}>
                    <CalcButton
                        onClick={() => {
                            addNumber('9')
                        }}>
                        9
                    </CalcButton>
                </Grid>
                <Grid item xs={2}>
                    <CalcButton
                        onClick={() => {
                            operatorPressed('/')
                        }}>
                        /
                    </CalcButton>
                </Grid>
                <Grid item xs={4}>
                    <CalcButton
                        value="%"
                        onClick={() => {
                            operatorPressed('%')
                        }}>
                        &#37;
                    </CalcButton>
                </Grid>

                <Grid item xs={2}>
                    <CalcButton
                        onClick={() => {
                            addNumber('4')
                        }}>
                        4
                    </CalcButton>
                </Grid>
                <Grid item xs={2}>
                    <CalcButton
                        onClick={() => {
                            addNumber('5')
                        }}>
                        5
                    </CalcButton>
                </Grid>
                <Grid item xs={2}>
                    <CalcButton
                        onClick={() => {
                            addNumber('6')
                        }}>
                        6
                    </CalcButton>
                </Grid>
                <Grid item xs={2}>
                    <CalcButton
                        onClick={() => {
                            operatorPressed('*')
                        }}>
                        *
                    </CalcButton>
                </Grid>
                <Grid item xs={4}>
                    <CalcButton
                        value="1/x"
                        onClick={() => {
                            operatorPressed('1/x')
                        }}>
                        ยน/ร
                    </CalcButton>
                </Grid>

                <Grid item xs={8}>
                    <Grid container xs={12} spacing={3} justify="center" alignItems="stretch">
                        <Grid item xs={3}>
                            <CalcButton
                                onClick={() => {
                                    addNumber('1')
                                }}>
                                1
                            </CalcButton>
                        </Grid>
                        <Grid item xs={3}>
                            <CalcButton
                                onClick={() => {
                                    addNumber('2')
                                }}>
                                2
                            </CalcButton>
                        </Grid>
                        <Grid item xs={3}>
                            <CalcButton
                                onClick={() => {
                                    addNumber('3')
                                }}>
                                3
                            </CalcButton>
                        </Grid>
                        <Grid item xs={3}>
                            <CalcButton
                                onClick={() => {
                                    operatorPressed('-')
                                }}>
                                -
                            </CalcButton>
                        </Grid>
                    </Grid>

                    <Grid container xs={12} spacing={3} justify="center" alignItems="stretch">
                        <Grid item xs={6}>
                            <CalcButton
                                onClick={() => {
                                    addNumber('0')
                                }}>
                                0
                            </CalcButton>
                        </Grid>
                        <Grid item xs={3}>
                            <CalcButton
                                onClick={() => {
                                    addNumber('.')
                                }}>
                                .
                            </CalcButton>
                        </Grid>
                        <Grid item xs={3}>
                            <CalcButton
                                onClick={() => {
                                    operatorPressed('+')
                                }}>
                                +
                            </CalcButton>
                        </Grid>
                    </Grid>
                </Grid>

                <Grid item xs={4}>
                    <Grid style={{ height: '100%' }}>
                        <CalcButton
                            value="="
                            onClick={() => {
                                resultPressed()
                            }}>
                            &#61;
                        </CalcButton>
                    </Grid>
                </Grid>
            </Grid>
        </Box>
    )
}
